import React from 'react'

export default function Pagination({ pageNo, pageNoUpdate }) {
    return (
        <>
            <hr />
            <div className="mx-2 d-flex justify-content-center">
                <ul className="pagination d-flex justify-content-between">

                    {pageNo === 1 ? 
                        <li className="page-item mx-2 disabled">
                            <span className="page-link text-primary" style={{cursor:"pointer"}} onClick={() => { pageNoUpdate(pageNo - 1) }}>PREV</span>
                        </li> 
                    : 
                        <li className="page-item mx-2">
                            <span className="page-link text-primary" style={{cursor:"pointer"}} onClick={() => { pageNoUpdate(pageNo - 1) }}>PREV</span>
                        </li>
                    }

                    <li className="page-item active pagination-circle" aria-current="page">
                        <span className="page-link" style={{cursor:"pointer"}}> {pageNo} <span className="visually-hidden">(current)</span></span>
                    </li>

                    <li className="page-item mx-2">
                        <span className="page-link text-primary" style={{cursor:"pointer"}} onClick={() => { pageNoUpdate(pageNo + 1) }} >NEXT</span>
                    </li>
                </ul>
            </div>
        </>
    )
}
