import './App.css';
import Movies from './components/Movies';
import Header from './components/Header';
import Pagination from './components/Pagination';
import Info from './components/Info';
import { useState, useEffect } from 'react';
import Swal from 'sweetalert2'



const Home_Suggestion_API = "https://api.themoviedb.org/3/discover/movie?with_genres=18&sort_by=vote_average.desc&vote_count.gte=10&sort_by=popularity.desc&api_key=a20e52f6f51cafc80ee323856e0e6623&page=";

function App() {


  const [movies, setMovies] = useState([]); //actual movies array to show to the users
  const [storeTempMovieResults,setStore] = useState([]); //storing to prevent api calling again and again if user clicks on the thunder movies logo
  //pagination
  const [pageNo, setpageNo] = useState(1) //starting page from 1

  let initFavouriteArray;
  if (localStorage.getItem("FAV_ARRAY") === null) {
    initFavouriteArray = [];
  } else {
    initFavouriteArray = JSON.parse(localStorage.getItem("FAV_ARRAY"));
  }

  //adding movies to favourites
  const addMovie = (movie) => {
    console.log("reaching to the destination !");
    console.log(movie);
    movie.delete = "yep"
    setFAV_ARRAY([...FAV_ARRAY, movie]); // this is basically how you can add a document or element in an array
  }

  const deleteMovie = (movie) => {
    Swal.fire({
      title: 'Are you sure?',
      showClass: {
        popup: 'animate__animated animate__zoomInDown animate__faster'
      },
      hideClass: {
        popup: 'animate__animated animate__zoomOut animate__faster'
      },
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {

      if (result.isConfirmed) {  //if user confirmed the delete operation
        setFAV_ARRAY(FAV_ARRAY.filter((e) => { //using setFAV_ARRAY that we have used below on the FAV_ARRAY
          return e !== movie; // if e is not eaual to the movie that we are receiving as a function argument 
        }))

        localStorage.setItem("FAV_ARRAY", JSON.stringify(FAV_ARRAY));

        Swal.fire(
          'Deleted!',
          'Your movie has been deleted.',
          'success'
        )
      }
    })
  }
  const [FAV_ARRAY, setFAV_ARRAY] = useState(initFavouriteArray);

  useEffect(() => {
    localStorage.setItem("FAV_ARRAY", JSON.stringify(FAV_ARRAY));//calling setItems to update the FAV array in localstorage immedietly if there is any changes in FAV array
    setMovies(FAV_ARRAY)
  }, [FAV_ARRAY])//FAV_ARRAY input array


  const PageNoUpdate = (page) => {

    setpageNo(page); // state setted but not immediatly reflected show for that we need to Call the updater below just to get the latest value.
    setpageNo((state) => {
      //state is the latest updated state
      async function fetchData1() {
        setMovies([]);
        const movieResponse = await (await fetch(Home_Suggestion_API + state)).json();
        setMovies(movieResponse.results);
        setStore(movieResponse.results);
      }
      fetchData1();
      return state;
    });

  }

  useEffect(() => {
    async function fetchData() {
      const movieResponse = await (await fetch(Home_Suggestion_API + 1)).json();
      setMovies(movieResponse.results);
      setStore(movieResponse.results);
    }
    fetchData();
  }, [])

  // search aaray set
  const onUpdate = (data) => {
    setMovies(data);
  }

  const showMoviesHomePage= ()=>{
    setMovies(storeTempMovieResults)
  }

  const setFav = () => {
    setMovies(FAV_ARRAY);
  }
  return (
    <div>
      <Header onUpdate={onUpdate} setFav={setFav} showMoviesHomePage={showMoviesHomePage}/>
      <div className="d-flex flex-wrap justify-content-center">
        {movies !== undefined ? movies.map((one_movie) => (
          <Movies key={one_movie.id} movie={one_movie} addMovie={addMovie} deleteMovie={deleteMovie} />
        )) : <Info/>}
      </div>

      { movies!==undefined && movies.length === 0 ? //showing the info if nothing to show
          <Info/>
        :
        ""
      }
      <Pagination pageNo={pageNo} pageNoUpdate={PageNoUpdate} />
    </div>
  );
}

export default App;
