import React from 'react'

export default function Info() {
    return (
        <div className="d-flex justify-content-center vh-100">
            
            <div className="list-group">
                    <p className="badge bg-danger p-3 rounded">Nothing to Show!</p>
                <span style={{cursor:"pointer"}} className="list-group-item list-group-item-action active rounded my-1" aria-current="true">
                    <div className="d-flex w-100 justify-content-between">
                        <h5 className="mb-1">Add your favorite movie here</h5>
                        <small>Now</small>
                    </div>
                    <p className="mb-1">
                        You can click the Add to Favorites button that appears on each movie card.
                    </p>
                    <small>The Add to Favorites button looks like this <i className="fas fa-bookmark mx-2"></i></small>
                </span>
                <span style={{cursor:"pointer"}} className="list-group-item list-group-item-action rounded my-1">
                    <div className="d-flex w-100 justify-content-between">
                        <h5 className="mb-1">Check Favorite List</h5>
                        <small className="text-muted">Now</small>
                    </div>
                    <p className="mb-1">
                    You can see your favorite list by clicking on the list icon on the top right corner near the search bar.</p>
                    <hr/>
                    <p className="mb-1">
                        Click on the Thunder Movie logo if you want to go back to the <span className="text-danger">previous results</span> from the Favorites list.
                    </p>
                    <small className="text-muted">The list icon looks like this <i className="fa fa-list mx-2" ></i></small>
                </span>
                <span style={{cursor:"pointer"}} className="list-group-item list-group-item-action rounded my-1">
                    <div className="d-flex w-100 justify-content-between">
                        <h5 className="mb-1">Search movies</h5>
                        <small className="text-muted">Now</small>
                    </div>
                    <p className="mb-1">
                        Enter movie name in the search bar and all results will be displayed automatically
                    </p>
                    <small className="text-muted">Results will be refreshed with a delay of 2 seconds</small>
                </span>
            </div>
        </div>
    )
}
